import { Component } from '@angular/core';
import { IonicPage, LoadingController, MenuController, NavController } from 'ionic-angular';

import { CompanyService } from '../../services';

import { ICompany } from '../../interfaces';

interface ICompanyPage {
  title:string;
  key:string;
}

@IonicPage({
  segment: 'home'
})
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  companiesPages:ICompanyPage[] = [];
  loading:any;
  showPage:boolean;

  constructor(
    public companyService: CompanyService,
    public loadingCtrl: LoadingController,
    public menuCtrl: MenuController,
    public navCtrl: NavController
  ){
    this.menuCtrl.enable(true);
    this.showLoading();
    this.getCompanies();
  }

  private showLoading(){
    this.loading = this.loadingCtrl.create({
      content: 'Carregando...'
    });

    this.loading.present();
  }

  private getCompanies(){
    this.companyService
      .getAll()
      .subscribe((companies:ICompany[]) => {
        companies = companies.filter(company => !company.hide);

        this.companiesPages = companies.map(company => {
          const { key, name } = company;

          return { key, title:name };
        });

        this.showPage = true;
        this.loading.dismiss();
      }, error => {
        this.loading.dismiss();
        console.error(error);
      } );
  }

  openCityPage(){
    this.navCtrl.push('CityPage');
  }
  openCompanyPage(key) {
    this.navCtrl.push('CompanyPage', { companyKey: key });
  }
}



